"use client"
import Link from 'next/link';
import React, { useState } from 'react';

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <nav className="p-4 ">
            <div className="container mx-auto flex flex-col lg:flex-row justify-between items-center">
                {/* Left Section (Logo & Mobile Menu Button) */}
                <div className="flex justify-between items-center w-full lg:w-auto">
                    <Link className="text-xl font-bold text-white " href="/">
                        Navbar
                    </Link>
                    {/* Mobile Menu Button */}
                    <button
                        className="block lg:hidden text-white focus:outline-none"
                        onClick={() => setIsOpen(!isOpen)}
                    >
                        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24"
                            xmlns="http://www.w3.org/2000/svg">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                d="M4 6h16M4 12h16m-7 6h7"></path>
                        </svg>
                    </button>
                </div>

                {/* Navigation Links */}
                <div className={`lg:flex lg:items-center lg:w-auto ${isOpen ? 'block' : 'hidden'} w-full lg:block mt-4 lg:mt-0`}>
                    <ul className="flex flex-col lg:flex-row lg:space-x-8 text-center">
                        <li><Link className="text-white hover:text-green-500 inline-flex items-center p-1 border border-transparent rounded hover:bg-gray-100 " href="/">Home</Link></li>
                        
                        {/* Dropdown for Services */}
                        <li className="relative group">
                            <Link className="text-white hover:text-green-500 inline-flex items-center p-1 border border-transparent rounded hover:bg-gray-100" href="/services">
                                Services
                            </Link>
                            <ul className="absolute left-0  w-48 rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible  ">
                                <li><Link className="block px-4 py-2 text-white " href="/services/web-development">Web Development</Link></li>
                                <li><Link className="block px-4 py-2 text-white " href="/services/app-development">App Development</Link></li>
                                <li><Link className="block px-4 py-2 text-white " href="/services/seo">SEO Services</Link></li>
                            </ul>
                        </li>

                        <li><Link className="text-white hover:text-green-500 inline-flex items-center p-1 border border-transparent rounded hover:bg-gray-100 " href="/how">How</Link></li>
                        <li><Link className="text-white hover:text-green-500 inline-flex items-center p-1 border border-transparent rounded hover:bg-gray-100 " href="/blog">Blog</Link></li>
                        <li><Link className="text-white hover:text-green-500 inline-flex items-center p-1 border border-transparent rounded hover:bg-gray-100 " href="/contact">Contact</Link></li>
                    </ul>
                </div>

                {/* Right Section (Buttons) */}
                <div className={`lg:flex lg:items-center lg:w-auto ${isOpen ? 'block' : 'hidden'} w-full lg:block mt-4 lg:mt-0`}>
                    <Link className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md bg-green-500 hover:bg-opacity-0 text-white " href="/login">
                        Login
                    </Link>
                    <Link className="ml-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md bg-green-500 hover:bg-opacity-0 text-white " href="/signup">
                        Sign Up
                    </Link>
                    <Link className="ml-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md bg-green-500 hover:bg-opacity-0 text-white" href="/profile">
                        Logout
                    </Link>
                    <Link className="ml-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md bg-green-500 hover:bg-opacity-0 text-white" href="/profile">
                        Profile
                    </Link>
                </div>
            </div>
        </nav>
    );
}
